// DSL
function DSLDemoPage() {
  const [activeStep, setActiveStep] = React.useState(0);
  const [isRunning, setIsRunning] = React.useState(false);
  const [results, setResults] = React.useState({});
  const [progress, setProgress] = React.useState(0);
  const [showDetails, setShowDetails] = React.useState(false);
  const [selectedAlgorithm, setSelectedAlgorithm] = React.useState(null);

  const dslExamples = [
    {
      title: 'ATSLP - ',
      description: '',
      code: `# ATSLP
from dsl import DSL, program

@program
def smart_city_coordination():
    dsl = DSL(workers=8)
    
    # 
    weather_task = dsl.gen("weather_monitor", 
                          prompt="",
                          agent="weather_agent")
                          .with_priority(1)
                          .with_timeout(5.0)
                          .schedule()
    
    traffic_task = dsl.gen("traffic_optimization",
                          prompt="",
                          agent="traffic_agent")
                          .with_priority(2)
                          .with_timeout(10.0)
                          .schedule()
    
    # 
    results = dsl.join([weather_task, traffic_task], 
                      mode="all", within_ms=5000)
    
    return results`,
      algorithm: 'ATSLP',
      features: ['', '', '', ''],
    },
    {
      title: 'HCMPL - ',
      description: '',
      code: `# HCMPL
@program
def cache_optimization():
    dsl = DSL()
    
    # 
    dsl.use_llm(llm_callable, use_cache=True)
    
    # 
    analysis_task = dsl.gen("data_analysis",
                          prompt="",
                          agent="analytics_agent")
                          .with_contract(Contract(
                              name="analysis-contract",
                              regex=r"\\d+\\s+patterns"
                          ))
                          .schedule()
    
    # 
    dsl.on("cache_hit", lambda data: print(f": {data}"))
    dsl.on("cache_miss", lambda data: print(f": {data}"))
    
    result = analysis_task.wait()
    return result`,
      algorithm: 'HCMPL',
      features: ['', '', '', ''],
    },
    {
      title: 'CALK - ',
      description: '',
      code: `# CALK
@program
def collaborative_learning():
    dsl = DSL()
    
    # 
    agent_group = ["traffic_agent", "parking_agent", "safety_agent"]
    
    # 
    for agent in agent_group:
        task = dsl.gen(f"learn_from_{agent}",
                      prompt=f"{agent}",
                      agent=agent)
                      .with_fallback("")
                      .schedule()
    
    # 
    dsl.on("knowledge_transfer", 
           lambda data: update_agent_knowledge(data))
    
    dsl.on("performance_improvement",
           lambda data: log_improvement(data))
    
    # 
    dsl.run()
    return dsl.get_history()`,
      algorithm: 'CALK',
      features: ['', '', '', ''],
    },
  ];

  const handleRunDemo = (index) => {
    setIsRunning(true);
    setProgress(0);
    setResults(prev => ({ ...prev, [index]: 'running' }));
    
    // 
    const interval = setInterval(() => {
      setProgress(prev => {
        if (prev >= 100) {
          clearInterval(interval);
          setResults(prev => ({ ...prev, [index]: 'completed' }));
          setIsRunning(false);
          return 100;
        }
        return prev + 10;
      });
    }, 200);
  };

  const handleShowDetails = (algorithm) => {
    setSelectedAlgorithm(algorithm);
    setShowDetails(true);
  };

  const handleCloseDetails = () => {
    setShowDetails(false);
    setSelectedAlgorithm(null);
  };

  return (
    <Container maxWidth="lg">
      <Box sx={{ py: 4 }}>
        {/* Header */}
        <Box sx={{ textAlign: 'center', mb: 6 }}>
          <Typography
            variant="h3"
            component="h1"
            gutterBottom
            sx={{ fontWeight: 700 }}
          >
            DSL
          </Typography>
          <Typography
            variant="h6"
            color="text.secondary"
            sx={{ maxWidth: 800, mx: 'auto' }}
          >
            ATSLPHCMPLCALK
          </Typography>
        </Box>

        {/* Algorithm Demonstrations */}
        <Box sx={{ mb: 6 }}>
          <Typography
            variant="h4"
            component="h2"
            gutterBottom
            sx={{ fontWeight: 600, mb: 4 }}
          >
            
          </Typography>

          <Stepper activeStep={activeStep} orientation="vertical">
            {dslExamples.map((example, index) => (
              <Step key={index}>
                <StepLabel>
                  <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
                    <Chip
                      label={example.algorithm}
                      color="primary"
                      size="small"
                    />
                    <Typography variant="h6">{example.title}</Typography>
                  </Box>
                </StepLabel>
                <StepContent>
                  <Card sx={{ mb: 3 }}>
                    <CardContent>
                      <Typography variant="body1" gutterBottom>
                        {example.description}
                      </Typography>
                      
                      <Box sx={{ my: 3 }}>
                        <Typography variant="h6" gutterBottom>
                          
                        </Typography>
                        <Paper
                          elevation={1}
                          sx={{
                            backgroundColor: '#f5f5f5',
                            overflow: 'auto',
                            maxHeight: 400,
                            p: 2,
                          }}
                        >
                          <pre style={{ 
                            fontFamily: 'Monaco, Consolas, "Courier New", monospace',
                            fontSize: '0.9rem',
                            margin: 0,
                            whiteSpace: 'pre-wrap',
                            wordBreak: 'break-word'
                          }}>
                            {example.code}
                          </pre>
                        </Paper>
                      </Box>

                      <Box sx={{ mb: 3 }}>
                        <Typography variant="h6" gutterBottom>
                          
                        </Typography>
                        <Box sx={{ display: 'flex', gap: 1, flexWrap: 'wrap' }}>
                          {example.features.map((feature, featureIndex) => (
                            <Chip
                              key={featureIndex}
                              label={feature}
                              size="small"
                              variant="outlined"
                            />
                          ))}
                        </Box>
                      </Box>

                      <Box sx={{ display: 'flex', gap: 2, alignItems: 'center', flexWrap: 'wrap' }}>
                        <Button
                          variant="contained"
                          startIcon={<PlayIcon />}
                          onClick={() => handleRunDemo(index)}
                          disabled={isRunning}
                          sx={{ minWidth: 120 }}
                        >
                          {results[index] === 'running' ? '...' : ''}
                        </Button>
                        
                        <Button
                          variant="outlined"
                          startIcon={<InfoIcon />}
                          onClick={() => handleShowDetails(example)}
                          sx={{ minWidth: 120 }}
                        >
                          
                        </Button>
                        
                        {results[index] === 'running' && (
                          <Box sx={{ flexGrow: 1, minWidth: 200 }}>
                            <LinearProgress 
                              variant="determinate" 
                              value={progress} 
                              sx={{ mb: 1 }}
                            />
                            <Typography variant="body2" color="text.secondary">
                              : {progress}%
                            </Typography>
                          </Box>
                        )}
                        
                        {results[index] === 'completed' && (
                          <Alert severity="success" sx={{ flexGrow: 1 }}>
                            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                              <CheckCircleIcon />
                              <Typography variant="body2">
                                {example.algorithm}
                              </Typography>
                            </Box>
                          </Alert>
                        )}
                      </Box>
                    </CardContent>
                  </Card>

                  <Box sx={{ mb: 2 }}>
                    <Button
                      variant="contained"
                      onClick={() => setActiveStep(index + 1)}
                      sx={{ mr: 1 }}
                    >
                      
                    </Button>
                    <Button
                      onClick={() => setActiveStep(index - 1)}
                      disabled={index === 0}
                    >
                      
                    </Button>
                  </Box>
                </StepContent>
              </Step>
            ))}
          </Stepper>
        </Box>
      </Box>

      {/* Algorithm Details Dialog */}
      <Dialog 
        open={showDetails} 
        onClose={handleCloseDetails}
        maxWidth="md"
        fullWidth
      >
        <DialogTitle>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
            <Typography variant="h5" sx={{ fontWeight: 600 }}>
              {selectedAlgorithm?.title} - 
            </Typography>
            <IconButton onClick={handleCloseDetails}>
              <CloseIcon />
            </IconButton>
          </Box>
        </DialogTitle>
        <DialogContent>
          {selectedAlgorithm && (
            <Box>
              <Typography variant="h6" gutterBottom sx={{ fontWeight: 600 }}>
                
              </Typography>
              <Typography variant="body1" paragraph>
                {selectedAlgorithm.description}
              </Typography>

              <Typography variant="h6" gutterBottom sx={{ fontWeight: 600, mt: 3 }}>
                
              </Typography>
              <Grid container spacing={2}>
                {selectedAlgorithm.features.map((feature, index) => (
                  <Grid item xs={6} key={index}>
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                      <CheckCircleIcon color="success" sx={{ fontSize: 20 }} />
                      <Typography variant="body2">{feature}</Typography>
                    </Box>
                  </Grid>
                ))}
              </Grid>

              <Typography variant="h6" gutterBottom sx={{ fontWeight: 600, mt: 3 }}>
                
              </Typography>
              <Paper
                elevation={1}
                sx={{
                  backgroundColor: '#f5f5f5',
                  overflow: 'auto',
                  maxHeight: 300,
                  p: 2,
                }}
              >
                <pre style={{ 
                  fontFamily: 'Monaco, Consolas, "Courier New", monospace',
                  fontSize: '0.9rem',
                  margin: 0,
                  whiteSpace: 'pre-wrap',
                  wordBreak: 'break-word'
                }}>
                  {selectedAlgorithm.code}
                </pre>
              </Paper>
            </Box>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDetails} variant="outlined">
            
          </Button>
          <Button onClick={handleCloseDetails} variant="contained">
            
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
}

// 12
function AgentsInterface() {
  const [agents, setAgents] = React.useState([
    { id: 1, name: 'Weather Agent', status: 'active', tasks: 23, efficiency: 96, avatar: '', description: '' },
    { id: 2, name: 'Traffic Agent', status: 'active', tasks: 45, efficiency: 94, avatar: '', description: '' },
    { id: 3, name: 'Parking Agent', status: 'active', tasks: 18, efficiency: 98, avatar: '', description: '' },
    { id: 4, name: 'Safety Agent', status: 'warning', tasks: 12, efficiency: 89, avatar: '', description: '' },
    { id: 5, name: 'EMS Agent', status: 'active', tasks: 8, efficiency: 97, avatar: '', description: '' },
    { id: 6, name: 'Energy Agent', status: 'active', tasks: 15, efficiency: 95, avatar: '', description: '' },
    { id: 7, name: 'Water Agent', status: 'active', tasks: 22, efficiency: 93, avatar: '', description: '' },
    { id: 8, name: 'Waste Agent', status: 'active', tasks: 19, efficiency: 91, avatar: '', description: '' },
    { id: 9, name: 'Security Agent', status: 'active', tasks: 31, efficiency: 96, avatar: '', description: '' },
    { id: 10, name: 'Education Agent', status: 'active', tasks: 14, efficiency: 92, avatar: '', description: '' },
    { id: 11, name: 'Healthcare Agent', status: 'active', tasks: 27, efficiency: 94, avatar: '', description: '' },
    { id: 12, name: 'Transport Agent', status: 'active', tasks: 33, efficiency: 97, avatar: '', description: '' },
  ]);

  const [selectedAgent, setSelectedAgent] = React.useState(null);
  const [showDetails, setShowDetails] = React.useState(false);

  const getStatusColor = (status) => {
    switch (status) {
      case 'active': return 'success';
      case 'warning': return 'warning';
      case 'error': return 'error';
      default: return 'default';
    }
  };

  const handleAgentClick = (agent) => {
    setSelectedAgent(agent);
    setShowDetails(true);
  };

  const handleCloseDetails = () => {
    setShowDetails(false);
    setSelectedAgent(null);
  };

  const handleToggleAgent = (agentId) => {
    setAgents(prev => prev.map(agent => 
      agent.id === agentId 
        ? { ...agent, status: agent.status === 'active' ? 'inactive' : 'active' }
        : agent
    ));
  };

  return (
    <Container maxWidth="lg">
      <Box sx={{ py: 4 }}>
        {/* Header */}
        <Box sx={{ textAlign: 'center', mb: 6 }}>
          <Typography
            variant="h3"
            component="h1"
            gutterBottom
            sx={{ fontWeight: 700 }}
          >
            
          </Typography>
          <Typography
            variant="h6"
            color="text.secondary"
            sx={{ maxWidth: 800, mx: 'auto' }}
          >
            12
          </Typography>
        </Box>

        {/* Statistics */}
        <Grid container spacing={3} sx={{ mb: 4 }}>
          <Grid item xs={12} sm={6} md={3}>
            <Card sx={{ textAlign: 'center' }}>
              <CardContent>
                <GroupIcon sx={{ fontSize: 40, color: 'primary.main', mb: 1 }} />
                <Typography variant="h4" sx={{ fontWeight: 700 }}>
                  {agents.length}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  
                </Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card sx={{ textAlign: 'center' }}>
              <CardContent>
                <CheckCircleIcon sx={{ fontSize: 40, color: 'success.main', mb: 1 }} />
                <Typography variant="h4" sx={{ fontWeight: 700 }}>
                  {agents.filter(a => a.status === 'active').length}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  
                </Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card sx={{ textAlign: 'center' }}>
              <CardContent>
                <HistoryIcon sx={{ fontSize: 40, color: 'info.main', mb: 1 }} />
                <Typography variant="h4" sx={{ fontWeight: 700 }}>
                  {agents.reduce((sum, agent) => sum + agent.tasks, 0)}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  
                </Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card sx={{ textAlign: 'center' }}>
              <CardContent>
                <SettingsIcon sx={{ fontSize: 40, color: 'warning.main', mb: 1 }} />
                <Typography variant="h4" sx={{ fontWeight: 700 }}>
                  {Math.round(agents.reduce((sum, agent) => sum + agent.efficiency, 0) / agents.length)}%
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  
                </Typography>
              </CardContent>
            </Card>
          </Grid>
        </Grid>

        {/* Agents Grid */}
        <Grid container spacing={3}>
          {agents.map((agent) => (
            <Grid item xs={12} sm={6} md={4} key={agent.id}>
              <Card 
                sx={{ 
                  cursor: 'pointer',
                  transition: 'transform 0.2s ease-in-out',
                  '&:hover': {
                    transform: 'translateY(-4px)',
                    boxShadow: 4,
                  }
                }}
                onClick={() => handleAgentClick(agent)}
              >
                <CardContent>
                  <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                    <Avatar sx={{ mr: 2, fontSize: '1.5rem' }}>
                      {agent.avatar}
                    </Avatar>
                    <Box sx={{ flexGrow: 1 }}>
                      <Typography variant="h6" sx={{ fontWeight: 600 }}>
                        {agent.name}
                      </Typography>
                      <Typography variant="body2" color="text.secondary">
                        {agent.description}
                      </Typography>
                    </Box>
                    <Chip 
                      label={agent.status} 
                      color={getStatusColor(agent.status)} 
                      size="small" 
                    />
                  </Box>
                  
                  <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 2 }}>
                    <Box>
                      <Typography variant="body2" color="text.secondary">
                        
                      </Typography>
                      <Typography variant="h6" sx={{ fontWeight: 600 }}>
                        {agent.tasks}
                      </Typography>
                    </Box>
                    <Box>
                      <Typography variant="body2" color="text.secondary">
                        
                      </Typography>
                      <Typography variant="h6" sx={{ fontWeight: 600 }}>
                        {agent.efficiency}%
                      </Typography>
                    </Box>
                  </Box>

                  <LinearProgress 
                    variant="determinate" 
                    value={agent.efficiency} 
                    sx={{ mb: 2 }}
                  />

                  <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                    <FormControlLabel
                      control={
                        <Switch
                          checked={agent.status === 'active'}
                          onChange={() => handleToggleAgent(agent.id)}
                          size="small"
                        />
                      }
                      label=""
                    />
                    <Button size="small" startIcon={<InfoIcon />}>
                      
                    </Button>
                  </Box>
                </CardContent>
              </Card>
            </Grid>
          ))}
        </Grid>
      </Box>

      {/* Agent Details Dialog */}
      <Dialog 
        open={showDetails} 
        onClose={handleCloseDetails}
        maxWidth="md"
        fullWidth
      >
        <DialogTitle>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              <Avatar sx={{ mr: 2, fontSize: '1.5rem' }}>
                {selectedAgent?.avatar}
              </Avatar>
              <Box>
                <Typography variant="h5" sx={{ fontWeight: 600 }}>
                  {selectedAgent?.name}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  {selectedAgent?.description}
                </Typography>
              </Box>
            </Box>
            <IconButton onClick={handleCloseDetails}>
              <CloseIcon />
            </IconButton>
          </Box>
        </DialogTitle>
        <DialogContent>
          {selectedAgent && (
            <Box>
              <Grid container spacing={3}>
                <Grid item xs={12} md={6}>
                  <Typography variant="h6" gutterBottom>
                    
                  </Typography>
                  <List dense>
                    <ListItem>
                      <ListItemText 
                        primary="" 
                        secondary={selectedAgent.tasks}
                      />
                    </ListItem>
                    <ListItem>
                      <ListItemText 
                        primary="" 
                        secondary={`${selectedAgent.efficiency}%`}
                      />
                    </ListItem>
                    <ListItem>
                      <ListItemText 
                        primary="" 
                        secondary={
                          <Chip 
                            label={selectedAgent.status} 
                            color={getStatusColor(selectedAgent.status)} 
                            size="small" 
                          />
                        }
                      />
                    </ListItem>
                  </List>
                </Grid>
                <Grid item xs={12} md={6}>
                  <Typography variant="h6" gutterBottom>
                    
                  </Typography>
                  <Box sx={{ mb: 2 }}>
                    <Typography variant="body2" color="text.secondary" gutterBottom>
                      
                    </Typography>
                    <LinearProgress 
                      variant="determinate" 
                      value={selectedAgent.efficiency} 
                      sx={{ mb: 1 }}
                    />
                    <Typography variant="body2">
                      {selectedAgent.efficiency}%
                    </Typography>
                  </Box>
                  <Box sx={{ mb: 2 }}>
                    <Typography variant="body2" color="text.secondary" gutterBottom>
                      
                    </Typography>
                    <LinearProgress 
                      variant="determinate" 
                      value={(selectedAgent.tasks / 50) * 100} 
                      color="secondary"
                      sx={{ mb: 1 }}
                    />
                    <Typography variant="body2">
                      {selectedAgent.tasks}/50 
                    </Typography>
                  </Box>
                </Grid>
              </Grid>
            </Box>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDetails} variant="outlined">
            
          </Button>
          <Button onClick={handleCloseDetails} variant="contained">
            
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
}

