function InteractionHistory() {
  const [interactions, setInteractions] = React.useState([
    {
      id: 1,
      timestamp: '2024-01-15 14:32:15',
      agent1: 'Weather Agent',
      agent2: 'Traffic Agent',
      type: '',
      status: '',
      description: '',
      avatar1: '',
      avatar2: ''
    },
    {
      id: 2,
      timestamp: '2024-01-15 14:28:42',
      agent1: 'Parking Agent',
      agent2: 'Safety Agent',
      type: '',
      status: '',
      description: '',
      avatar1: '',
      avatar2: ''
    },
    {
      id: 3,
      timestamp: '2024-01-15 14:25:18',
      agent1: 'EMS Agent',
      agent2: 'Transport Agent',
      type: '',
      status: '',
      description: '',
      avatar1: '',
      avatar2: ''
    },
    {
      id: 4,
      timestamp: '2024-01-15 14:20:33',
      agent1: 'Energy Agent',
      agent2: 'Water Agent',
      type: '',
      status: '',
      description: '',
      avatar1: '',
      avatar2: ''
    },
    {
      id: 5,
      timestamp: '2024-01-15 14:15:27',
      agent1: 'Security Agent',
      agent2: 'Healthcare Agent',
      type: '',
      status: '',
      description: '',
      avatar1: '',
      avatar2: ''
    },
  ]);

  const [filter, setFilter] = React.useState('all');
  const [showDetails, setShowDetails] = React.useState(false);
  const [selectedInteraction, setSelectedInteraction] = React.useState(null);

  const getStatusColor = (status) => {
    switch (status) {
      case '': return 'success';
      case '': return 'warning';
      case '': return 'error';
      default: return 'default';
    }
  };

  const handleInteractionClick = (interaction) => {
    setSelectedInteraction(interaction);
    setShowDetails(true);
  };

  const handleCloseDetails = () => {
    setShowDetails(false);
    setSelectedInteraction(null);
  };

  const filteredInteractions = filter === 'all' 
    ? interactions 
    : interactions.filter(i => i.status === filter);

  return (
    <Container maxWidth="lg">
      <Box sx={{ py: 4 }}>
        {/* Header */}
        <Box sx={{ textAlign: 'center', mb: 6 }}>
          <Typography
            variant="h3"
            component="h1"
            gutterBottom
            sx={{ fontWeight: 700 }}
          >
            
          </Typography>
          <Typography
            variant="h6"
            color="text.secondary"
            sx={{ maxWidth: 800, mx: 'auto' }}
          >
            
          </Typography>
        </Box>

        {/* Filter Controls */}
        <Box sx={{ mb: 4, display: 'flex', gap: 2, justifyContent: 'center', flexWrap: 'wrap' }}>
          <Button
            variant={filter === 'all' ? 'contained' : 'outlined'}
            onClick={() => setFilter('all')}
          >
             ({interactions.length})
          </Button>
          <Button
            variant={filter === '' ? 'contained' : 'outlined'}
            onClick={() => setFilter('')}
            color="success"
          >
             ({interactions.filter(i => i.status === '').length})
          </Button>
          <Button
            variant={filter === '' ? 'contained' : 'outlined'}
            onClick={() => setFilter('')}
            color="warning"
          >
             ({interactions.filter(i => i.status === '').length})
          </Button>
          <Button
            variant={filter === '' ? 'contained' : 'outlined'}
            onClick={() => setFilter('')}
            color="error"
          >
             ({interactions.filter(i => i.status === '').length})
          </Button>
        </Box>

        {/* Interactions List */}
        <Paper elevation={2}>
          <List>
            {filteredInteractions.map((interaction, index) => (
              <React.Fragment key={interaction.id}>
                <ListItem 
                  sx={{ 
                    cursor: 'pointer',
                    '&:hover': { backgroundColor: 'action.hover' }
                  }}
                  onClick={() => handleInteractionClick(interaction)}
                >
                  <ListItemAvatar>
                    <Avatar sx={{ bgcolor: 'primary.main' }}>
                      <ChatIcon />
                    </Avatar>
                  </ListItemAvatar>
                  <ListItemText
                    primary={
                      <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
                        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                          <Avatar sx={{ width: 24, height: 24, fontSize: '0.8rem' }}>
                            {interaction.avatar1}
                          </Avatar>
                          <Typography variant="body2">
                            {interaction.agent1}
                          </Typography>
                        </Box>
                        <Typography variant="body2" color="text.secondary">
                          
                        </Typography>
                        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                          <Avatar sx={{ width: 24, height: 24, fontSize: '0.8rem' }}>
                            {interaction.avatar2}
                          </Avatar>
                          <Typography variant="body2">
                            {interaction.agent2}
                          </Typography>
                        </Box>
                      </Box>
                    }
                    secondary={
                      <Box>
                        <Typography variant="body2" color="text.secondary">
                          {interaction.description}
                        </Typography>
                        <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, mt: 1 }}>
                          <Chip 
                            label={interaction.type} 
                            size="small" 
                            variant="outlined"
                          />
                          <Chip 
                            label={interaction.status} 
                            color={getStatusColor(interaction.status)}
                            size="small" 
                          />
                          <Typography variant="caption" color="text.secondary">
                            {interaction.timestamp}
                          </Typography>
                        </Box>
                      </Box>
                    }
                  />
                  <ListItemSecondaryAction>
                    <IconButton edge="end">
                      <VisibilityIcon />
                    </IconButton>
                  </ListItemSecondaryAction>
                </ListItem>
                {index < filteredInteractions.length - 1 && <Divider />}
              </React.Fragment>
            ))}
          </List>
        </Paper>

        {/* Add New Interaction FAB */}
        <Fab
          color="primary"
          sx={{ position: 'fixed', bottom: 16, right: 16 }}
          onClick={() => {
            const newInteraction = {
              id: interactions.length + 1,
              timestamp: new Date().toLocaleString('zh-CN'),
              agent1: 'New Agent',
              agent2: 'System',
              type: '',
              status: '',
              description: '',
              avatar1: '',
              avatar2: ''
            };
            setInteractions(prev => [newInteraction, ...prev]);
          }}
        >
          <AddIcon />
        </Fab>
      </Box>

      {/* Interaction Details Dialog */}
      <Dialog 
        open={showDetails} 
        onClose={handleCloseDetails}
        maxWidth="sm"
        fullWidth
      >
        <DialogTitle>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
            <Typography variant="h5" sx={{ fontWeight: 600 }}>
              
            </Typography>
            <IconButton onClick={handleCloseDetails}>
              <CloseIcon />
            </IconButton>
          </Box>
        </DialogTitle>
        <DialogContent>
          {selectedInteraction && (
            <Box>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, mb: 3 }}>
                <Avatar sx={{ bgcolor: 'primary.main' }}>
                  {selectedInteraction.avatar1}
                </Avatar>
                <Typography variant="h6">
                  {selectedInteraction.agent1}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  
                </Typography>
                <Typography variant="h6">
                  {selectedInteraction.agent2}
                </Typography>
                <Avatar sx={{ bgcolor: 'secondary.main' }}>
                  {selectedInteraction.avatar2}
                </Avatar>
              </Box>

              <Typography variant="h6" gutterBottom>
                
              </Typography>
              <List dense>
                <ListItem>
                  <ListItemText 
                    primary="" 
                    secondary={selectedInteraction.type}
                  />
                </ListItem>
                <ListItem>
                  <ListItemText 
                    primary="" 
                    secondary={
                      <Chip 
                        label={selectedInteraction.status} 
                        color={getStatusColor(selectedInteraction.status)}
                        size="small" 
                      />
                    }
                  />
                </ListItem>
                <ListItem>
                  <ListItemText 
                    primary="" 
                    secondary={selectedInteraction.timestamp}
                  />
                </ListItem>
                <ListItem>
                  <ListItemText 
                    primary="" 
                    secondary={selectedInteraction.description}
                  />
                </ListItem>
              </List>
            </Box>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDetails} variant="outlined">
            
          </Button>
          <Button onClick={handleCloseDetails} variant="contained">
            
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
}

